@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');
body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, dl, dt, dd, button, table, tr, td, th {margin: 0; padding: 0;}
li {list-style: none;}
a {text-decoration: none; color: #fff;}
button {background: none; border: 0; cursor: pointer;}
img {display: block;}
body {
  font-family: 'Poppins', 'NanumSquare', sans-serif; 
  font-size: 16px;
  letter-spacing: -1px; 
  word-break: keep-all;
  color: #fff;
}

/* 1. 모바일 CSS 스크린 폭 (7인치): 500px이하 */
@media screen and (max-width: 500px) {
  /* 1) 전체 배경, header, nav 설정 */
  .wrap {background: linear-gradient(-225deg, #2CD8D5 0%, #6B8DD6 48%, #8E37D7 100%);}
  .header, .nav_close_btn {
    height: 60px; 
    border-bottom: 1px solid rgba(255,255,255,0.2); 
    padding: 0 5%; 
    box-sizing: border-box;
    display: flex; 
    align-items: center;
  }
  .header {position: sticky; top: 0; left: 0; z-index: 1; width: 100%; background: rgba(255,255,255,0.3);}
  h1 img {width: 60px; height: auto;}
  .nav_open_btn {position: fixed; top: 17px; right: 5%; z-index: 1;}
  .nav_open_btn button, .nav_close_btn button {width: 25px; height: 25px;}
  .nav_close_btn {justify-content: right;}
  nav {
    display: none;
    position: fixed; 
    top: 0; 
    left: 0; 
    z-index: 2;
    width: 100%; 
    height: 100%; 
    background: linear-gradient(-225deg, #2CD8D5 0%, #6B8DD6 48%, #8E37D7 100%);
  }
  .gnb a {display: block; height: 70px; line-height: 70px; text-align: center; font-size: 1.1rem;}
  .gnb a:hover {color: #151515;}

  /* 2-1) 섹션 공통 - 높이, 면적, 여백, 콘텐츠 배경, fadein 효과 */
  section>div {padding: 0 5% 150px;}
  .intro li, .profile_desc, .career_desc, .certificate_list, .edu_subject, .contact_detail { 
    border-radius: 50px; 
    padding: 10%; 
    background: rgba(0,0,0,0.5); 
    box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
  }
  @keyframes fadein {
    0% {opacity: 0; transform: translateY(20px);}
    100% {opacity: 1; transform: none;}
  }

  /* 2-2) 섹션 공통 - 섹션 제목(component) */
  .section_title {margin: 50px 0; text-align: center; text-shadow: 0 2px 2px #151515;}
  .section_title h2 {margin-bottom: 10px; font-size: 2.2rem;}
  .section_title p {
    display: inline-block; 
    border-radius: 30px;
    padding: 5px 20px; 
    color: #00d3dd;
    background: #151515; 
    box-shadow: 2px 4px 4px rgba(0,0,0,0.3);
  }

  /* 3) 섹션1 - intro */
  .intro1 {animation: fadein 0.5s ease-in-out;}
  .intro2 {animation: fadein 1s ease-in-out;}
  .intro3 {animation: fadein 1.5s ease-in-out;}
  .intro li {display: flex; flex-wrap: wrap; align-items: center;}
  .intro li:nth-child(2) {margin: 50px 0;}
  .intro li p:nth-child(1) {width: 60px; font-size: 3rem; font-weight: 500; color: #00d3dd; opacity: 0.5;}
  .intro li p:nth-child(2) {width: 65%; margin-left: 10px; font-size: 1.2rem;}

  /* 4) 섹션2 - profile */
  .profile_desc {line-height: 1.6; animation: fadein 0.5s ease-in-out;}
  .profile_desc p:first-child {margin-bottom: 20px; font-size: 1.2rem; color: #00d3dd;}
  .profile_list>li>p {
    height: 25px; 
    border-left: 5px solid #151515; 
    padding-left: 10px; 
    margin-bottom: 20px;
    line-height: 25px;
    font-size: 1.5rem; 
    font-weight: 500; 
    color: #151515;
  }
  .career {margin: 50px 0; animation: fadein 1s ease-in-out;}
  .career_desc {padding: 30px 10%;}
  .career_desc p:nth-child(1), .certificate_list p:nth-child(1) {opacity: 0.5;}
  .career_detail {padding-top: 15px; margin-top: 15px; border-top: 1px solid rgba(255,255,255,0.3);}
  .certificate {animation: fadein 1.5s ease-in-out;}
  .certificate_list {padding: 25px 10%;}
  .certificate_list li {display: flex;}
  .certificate_list p:nth-child(1) {width: 100px;}
  .certificate_list p:nth-child(3) {display: none;}

  /* 5) 섹션3 - education */
  .edu_list span {padding: 0 10px; opacity: 0.5;}
  .edu_list>li>p {padding-left: 10px; margin-bottom: 30px; color: #151515;}
  .edu_list>li>p::before {
    display: block;
    position: absolute;
    content: ""; 
    width: 3px; 
    height: 16px; 
    margin: 5px 0 0 -10px;
    background: #151515;
  }
  .edu_list>li:first-child {animation: fadein 0.5s ease-in-out;}
  .edu_list>li:last-child {animation: fadein 1s ease-in-out;}
  .edu_subject {animation: fadein 1.5s ease-in-out;}
  .edu_subject li:not(:last-child) {
    border-bottom: 1px solid rgba(255,255,255,0.2);
    margin-bottom: 20px; 
    padding-bottom: 20px; 
  }
  .edu_subject p:first-child {color: #00d3dd;}

  /* 6) 섹션4 - portfolio */
  .work_list {
    display: flex; 
    flex-wrap: wrap; 
    justify-content: space-between; 
    animation: fadein 0.5s ease-in-out;
  }
  .work_list li {
    width: 45%;
    border: 2px solid #151515;
    border-radius: 20px; 
    overflow: hidden;
    text-align: center;
    font-size: 0.8rem;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
    animation: fadein 1s ease-in-out;
  }
  .work_list li:not(:nth-child(n+7)) {margin-bottom: 50px;}
  .work_list li::before {display: block; font-size: 1rem; color: #00d3dd; background: rgba(0,0,0,0.8);}
  .work_list li:nth-child(-n+4)::before {content: "web";}
  .work_list li:nth-child(n+5)::before {content: "2D";}
  .work_list li img {max-width: 100%; height: auto;}
  .work_list li:hover img {filter: brightness(35%);}
  .work_title {height: 60px; padding: 15% 10%; color: #fff; background: rgba(0,0,0,0.8);}
  .work_list li:nth-child(7) .work_title {padding: 13% 10% 17%;}
  .quick_btn {display: none;}

  /* 7) 섹션5 - contact */
  .contact {background: url(../public/images/contact_icon.png) no-repeat right bottom 5%/70% auto;}
  .contact .section_title p {display: none;}
  .contact_detail {animation: fadein 1s ease-in-out;}
  .contact_detail p:nth-child(1) {
    width: 230px; 
    border-right: 3px solid;
    margin: 0 auto; 
    overflow: hidden; 
    white-space: nowrap;
    font-size: 1.2rem; 
    color: #00d3dd; 
    animation: typing 2s steps(17), blink 0.5s step-end infinite alternate;
  }
  @keyframes typing {from {width: 0;}}
  @keyframes blink {50% {border-color: transparent;}}
  .contact_detail p:not(:nth-child(4)) {margin-bottom: 50px;}
  .contact p:nth-child(2) {text-align: right;}
  .contact p:nth-child(4) {font-weight: 500;}
  .email {
    display: inline-block;
    background: linear-gradient(transparent calc(100% - 3px), #000 3px) no-repeat;
    background-size: 0% 100%;
    transition: background-size 0.8s;
  }
  .email {background-image: linear-gradient(transparent 60%, #00d3dd 40%);}
  .email:hover {background-size: 100% 100%;}

  /* 8) footer */
  .footer {padding: 25px 5%; text-align: center; font-size: 0.9rem; background: #151515;}
}

