/* 3. 데스크탑 CSS 스크린 폭 (포탈 사이트 기준): 1200px이상 */
@media screen and (min-width: 1200px) {
  /* 1) 기본 글자 크기, 전체 배경, header, nav 설정 */
  body {font-size: 17px;}
  .wrap {background: linear-gradient(-225deg, #2CD8D5 0%, #6B8DD6 48%, #8E37D7 100%);}
  .header {padding: 20px; border-bottom: 1px solid rgba(255,255,255,0.3);}
