  .gnb a {position: relative; margin: 0 20px; font-size: 20px;}
  .gnb a:after {
    content: ""; 
    position: absolute; 
    left: 0; 
    bottom: -5px; 
    width: 0px; 
    height: 5px; 
    transition: all 0.3s ease-in-out;
    background: #00d3dd;
  }
  .gnb a:hover {color: #00d3dd;}
  .gnb a:hover:after {width: 100%;}

  /* 2-1) 섹션 공통 - 높이, 면적, 여백, 콘텐츠 배경, fadein 효과 */
